import axios from "axios";
import { api } from "@/services/config.js";
console.log(api);

//
export function host_hosts(queryString) {
  return axios({
    headers: {
      "Access-Control-Allow-Origin": "*",
      "Content-Type": "application/json",
      "Access-Control-Allow-Methods": "GET,PUT,POST,DELETE",
    },

    method: "GET",
    url: `${api}/host/GetHost_List?${queryString}`,
    mode: "no-cors",
  });
}
export function create_host_host(body) {
  return axios({
    headers: {
      "Access-Control-Allow-Origin": "*",
      "Content-Type": "application/json",
      "Access-Control-Allow-Methods": "GET,PUT,POST,DELETE",
    },

    method: "POST",
    url: "${api}/host_detail/Save_Host",
    data: body,
    mode: "no-cors",
  });
}
export function update_host_host(body) {
  return axios({
    headers: {
      "Access-Control-Allow-Origin": "*",
      "Content-Type": "application/json",
      "Access-Control-Allow-Methods": "GET,PUT,POST,DELETE",
    },

    method: "POST",
    url: "${api}/host_detail/Save_Host",
    data: body,
    mode: "no-cors",
  });
}
export function delete_host_host(body) {
  return axios({
    headers: {
      "Access-Control-Allow-Origin": "*",
      "Content-Type": "application/json",
      "Access-Control-Allow-Methods": "GET,PUT,POST,DELETE",
    },

    method: "DELETE",
    url: "${api}/host/Delete_Host",
    data: { idxs: [`${body.idx}`] },
    mode: "no-cors",
  });
}
